define('truth-seeker-ember/components/truth-map', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({
    classNames: ["map-canvas"],
    markers: [],

    selectedTruth: "",

    insertMap: (function () {
      var container = this.$()[0];

      var options = {
        center: new window.google.maps.LatLng(this.get("latitude"), this.get("longitude")),
        zoom: 4,
        mapTypeId: google.maps.MapTypeId.ROADMAP,
        styles: this.styles,
        zoomControl: true,
        zoomControlOptions: {
          style: google.maps.ZoomControlStyle.MEDIUM,
          position: google.maps.ControlPosition.LEFT_CENTER
        },
        mapTypeControl: false,
        scaleControl: true,
        streetViewControl: true };

      this.set("map", new window.google.maps.Map(container, options));
      this.renderTruths();
    }).on("didInsertElement"),

    renderTruths: function renderTruths() {
      var _this = this;

      this.get("markers").forEach(function (marker) {
        marker.setMap(null);
      });
      this.get("truths").forEach(function (truth) {
        _this.placeTruthMarker(truth);
      });
    },

    updateTruths: (function () {
      var markers = this.get("markers");
      var truths = this.get("truths");
      this.renderTruths();
    }).observes("truths"),

    placeTruthMarker: function placeTruthMarker(truth) {
      var icon = {
        url: "/assets/images/" + this.get("category").toLowerCase() + ".png",
        scaledSize: new google.maps.Size(25, 30),
        origin: null,
        anchor: null };

      var marker = new google.maps.Marker({
        position: new window.google.maps.LatLng(truth.get("lat"), truth.get("long")),
        map: this.get("map"),
        title: truth.get("text"),
        animation: google.maps.Animation.DROP,
        icon: icon,
        truth: truth,
        component: this
      });

      marker.addListener("click", function () {
        this.component.set("selectedTruth", truth);
      });
      this.markers.pushObject(marker);
    },

    styles: [{
      featureType: "water",
      elementType: "all",
      stylers: [{
        color: "#758074"
      }, {
        visibility: "on"
      }]
    }, {
      featureType: "landscape.man_made",
      elementType: "all",
      stylers: [{
        hue: "#007F1E"
      }, {
        saturation: 100
      }, {
        lightness: -72
      }, {
        visibility: "on"
      }]
    }, {
      featureType: "landscape.natural",
      elementType: "all",
      stylers: [{
        hue: "#00C72E"
      }, {
        saturation: 100
      }, {
        lightness: -59
      }, {
        visibility: "on"
      }]
    }, {
      featureType: "road",
      elementType: "all",
      stylers: [{
        hue: "#002C0A"
      }, {
        saturation: 100
      }, {
        lightness: -87
      }, {
        visibility: "on"
      }]
    }, {
      featureType: "poi",
      elementType: "all",
      stylers: [{
        hue: "#00A927"
      }, {
        saturation: 100
      }, {
        lightness: -58
      }, {
        visibility: "on"
      }]
    }]
  });

});